import React, { useState } from "react";

import ExpenseDate from "./ExpenseDate";
import Card from "../UI/Card";
import "./ExpenseItem.css";

const ExpenseItem = (props) => {
  const [title, setTitle] = useState(props.title); // [current title variable, and new title variable], always returns an array
  console.log("ExpenseItem evaluated by React");

  const clickhandler = () => {
    setTitle("Updated"); //state the updating function
    console.log(title);
  };

  return (
    <Card className="expense-item">
      <ExpenseDate date={props.date} />
      <div className="expense-item__description">
        <h2>{title}</h2>
        <div className="expense-item__price">${props.amount}</div>
      </div>
      <button onClick={clickhandler}>Change Title</button>
    </Card> //the button happen something when clicked
  );
};

export default ExpenseItem;
